	module.exports = {"data":{"wordpressPost":{"title":"These Banging New Brow Products Are SO Awesome + Super Easy To Use","content":"<p>You know the perfectly-shaped brow can make or break your look. But how to achieve it?</p>\n<p>Sure, we could give you all the tricky measurements and talk about exactly where your brow should end in reference to your pupil and the inner corner of your eye. Hit you with a complex mathematical equation or two. But you know what &#8211; we&#8217;re not going to waste your time. Because the truth is, only you will know when your own brows are shaped just right.</p>\n<p>What we are happy to do though is point you in the direction of some truly ace products that will help make your brow dreams a reality.</p>\n<img class=\"size-medium wp-image-2454 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01192029/11151305_ULEyebrowStylus_Brunette_HR1-206x300.jpg\" alt=\"\" width=\"206\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01192029/11151305_ULEyebrowStylus_Brunette_HR1-206x300.jpg 206w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01192029/11151305_ULEyebrowStylus_Brunette_HR1-768x1118.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01192029/11151305_ULEyebrowStylus_Brunette_HR1-703x1024.jpg 703w\" sizes=\"(max-width: 206px) 100vw, 206px\" />\n<p>&nbsp;</p>\n<p><a href=\"http://www.ulta3.com.au\">Ulta3 Hello Brows 2-In-1 Stylus, $9.95.</a></p>\n<p>This wind-up pencil has a cool, angled tip that provides super natural-looking looking brow colour, depth and definition. Once you&#8217;ve drawn in your brows (light, mini strokes please) apply the clear setting glee to ensure they stay put. Available in blonde and brunette.</p>\n<img class=\"size-medium wp-image-2455 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01193322/HR_Brow_Shake_Powder_Dark_Brown-1-84x300.jpg\" alt=\"\" width=\"84\" height=\"300\" />\n<p><a href=\"https://www.priceline.com.au/brand/rimmel?cat=8\">Rimmel London Brow Shake Filling Powder, $14.95.</a></p>\n<p>A clever brow powder that offers a bold, sculpted look that lasts up to 24 hours! Perfect for those desk to date nights. It has a It features an ultra-soft applicator for easy, precise application. And importantly, comes in in four shades: Light Brown, Medium Brown, Dark Brown and Soft Black.</p>\n<img class=\"size-medium wp-image-2456 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01193916/BROW-FIBRE-EXTEND-SUITE-264x300.jpg\" alt=\"\" width=\"264\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01193916/BROW-FIBRE-EXTEND-SUITE-264x300.jpg 264w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01193916/BROW-FIBRE-EXTEND-SUITE-768x873.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01193916/BROW-FIBRE-EXTEND-SUITE-901x1024.jpg 901w\" sizes=\"(max-width: 264px) 100vw, 264px\" />\n<p><a href=\"http://retailers.eyeofhoruscosmetics.com\">Eye of Horus Brow Fibre Extend, $38.</a></p>\n<p>This twofer eyebrow product offers instant definition and volume in the one easy application. The formula contains seaweed and moringa oil to enhance the brows, and it lengthens and thickens brows upon application, adding natural-looking fibres. Available in three shades and a damn good buy.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/01191137/Ulta3_elyse20HelloBrows_2_HR1.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"25b45f65-5500-5c44-82d8-1ce60eb3aa72"}}

